import styled from 'styled-components'
import {Link as LinkR} from 'react-router-dom'
import {Link as LinkS} from 'react-scroll'

export const Nav = styled.nav`
    background: #FFFFFF;
    color: #707070;
    height: 80px;
    /* margin-top:-80px; */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: sticky;
    top: 0;
    z-index: 10;

    @media screen and (max-width: 960px){
        transition: 0.8s all ease;
    }
`

export const NavbarContainer = styled.div`
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 1;
    width: 100%;
    padding: 0 24px;
    max-width: 1100px;
    color: #707070;    
`
 export const NavLogo = styled(LinkR)`
    color: black;
    justify-self: flex-start;
    cursor: pointer;
    font-size: 2.7rem;
    display: flex;
    align-items: center;
    margin-left: 1px;
    font-weight: bold;
    text-decoration: none;
 `;

 export const MobileIcon = styled.div`
    display: none;

    @media screen and (max-width:768px){
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        color: #707070;
    }
 `
  
 export const NavMenu = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
    margin-right: -22px;
    
    @media screen and (max-width: 768px){
        display: none;
    }
 `

 export const NavItem = styled.li`
    height: 80px;
 `


export const NavLinks = styled(LinkS)`
    color: #707070;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding 0 1rem;
    height: 100%;
    cursor: pointer;

    &.active {
        border-bottom: 3px solid #01bf71;
    }

    &:hover {
        background-color: #00E0FF;
        color: white;
      }
`;

export const NavBtn = styled.nav`
    display: flex;
    align-items: center;
     
    #inputID::placeholder{
        color: #707070;
        padding: 15px;
        font-size: 0.8rem;
        border-color:red;
    }

    @media (max-width: 768px){
            display:none;
    }
`

export const NavBtnLink = styled(LinkR)`
    background: #00D6FF;
    white-space: nowrap;
    padding: 5px;
    color: #010606;
    font-size: 13px;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    

    &:hover{
        transition: all 0.2s ease-in-out;
        background: #fff;
        color: #010606;
    }

    
}
`